@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Signika+Negative:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;1,300&display=swap);
:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: 'Secular One', sans-serif;
  color: #4e4e4e;
  color: var(--secondary);
}
.App{
  margin: 0 auto;
}

.container{
  background-color: #F2F2F2;
  max-width: 540px;
  padding: 50px;
}

.Nav {
  background-color: #000000 !important;
  font-family: helvetica;
  font-weight: bolder;
  font-size: 15px;
  letter-spacing: 1px;
  text-align: right;
  text-indent: 0vmax;

}
h2 {
  color:black;
}

h3{

  min-height: 20vh;
  position: relative;
  display: flex;

}

.nav.brand {
  font-size: 25px;
}
.nav-links {
  width: 40%;
  right: 80px;
  min-height: 20vh;
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  list-style: none;
}


.title h2 {
  text-align: center;
  color: black;
  font-weight: bolder;

}

.title p1{
  text-align: center;
  color: white;
  font-weight: bolder;
  font-size: 1.5vw;
  border: 10px solid #1123e8;
  margin: auto;
  width: 12%;
  border-radius: 30%;
  background-color:#1123e8;
  display: block;  
}


.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
  
}

/* upload form styles */
form{
  margin: 30px auto 10px;
  text-align: center;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label{
  display: block;
  text-align: center;
  width: 30px;
  height: 30px;
  border: 1px solid #efb6b2;
  border: 1px solid var(--primary);
  border-radius: 40%;
  margin: 10px auto;
  line-height: 20px;
  color: #efb6b2;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}
label:hover{
  background: #efb6b2;
  background: var(--primary);
  color: white;
}
.output{
  height: 20px;
  font-size: 0.8rem;
}
.error{
  color: #ff4a4a;
  color: var(--error);
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: #efb6b2;
  background: var(--primary); 
  margin-top: 20px;
}

/* image grid styles */
.img-grid{
  margin: 40px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 960px;
  
  
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0; 
  /*padding controls height, will always be perferctly square regardless of width */
  position: relative;
  opacity: 0.8;
  border-radius: 8%;
}
.img-wrap img{
  min-width: 100%; 
  min-height: 100%; 
  max-width: 150%; 
  position: absolute; 
  top: 0; 
  left: 0;
  
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0; 
  left: 0; 
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5)
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid rgb(36, 36, 36);
}
